<script>
import page from "./v.s.page.vue"
export default {
    name:"CARD",
    extends:page,
    computed:{
         background(){
             return [
                {
                    // i:'url(https://images.static-collegedunia.com/public/college_data/images/campusimage/1473168697hostel.JPG)',
                    attach:true,
                    letter:true,
                     pos:'center',
                    size:'contain'
                },{
                    blur:true,
                    c:'rgb(0,0,0,0.3)'
                }];
            
        },
    },
}
</script>
<<style lang="less">
.CARD.PAGE.ROOT
{
    overflow:overlay; 
    border-radius:1em;
    width:var(--card-width,300px);
    height:var(--card-height,400px);
    filter: drop-shadow(0px 0px 12px #000);
    box-shadow: 0px 0px 6px -2px wheat;
    .BOX
    {
        
        #content-box
        {
            text-align: var(--text-align,center);
            align-content:end;
            mix-blend-mode: multiply;
        }
    }
    #page-background-0
    {
        // background-image:url(../assets/wall/khushi.jpg);
    } 
}

</style>>


